.rifle-drilling__load-item {
  display: flex;
  align-items: center;
}

.rifle-drilling__load-item::before,
.rifle-drilling__load-item::after {
  content: "";
  background-image: url(../assets/icons/download.svg);
  background-repeat: no-repeat;
  background-position: center;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin-right: 12px;
}
.rifle-drilling__load-item::before {
  background-image: url(../assets/icons/download.svg);
}

.rifle-drilling__load-item::after {
  background-image: url(../assets/icons/PDF.svg);
  margin-left: 9px;
  margin-top: -18px;
}

.rifle-drilling__load-item a {
  font-weight: 500;
  color: $c_blue;
  line-height: 34px;
  letter-spacing: 0.05px;
}

